<template lang="html">
  <div>
    <div class="session__header">
      <div class="session__header--left">
        <div class="view__header__title">
          <div>
            <h1>Sessions</h1>
          </div>
        </div>
        <div class="view__header tabs">
          <div class="view__header__menu">
            <ul>
              <li :class="[ sessionTabs === 'history' ? 'border--active' : '']">
                <a @click="changeTab('history')">
                  History
                </a>
              </li>
              <li :class="[ sessionTabs === 'upcoming' ? 'border--active' : '']">
                <a @click="changeTab('upcoming')">
                  Upcoming
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="session__header--right" v-if="stateResizeScreen == 3 || stateResizeScreen == 2">
        <span>Filter sessions by date range</span>
        <div class="header__filter">
          <label>From</label>
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="start_time"
                append-icon="event"
                @click="togleDatepicker"
              ></v-text-field>
              <v-date-picker
                v-model="start_time"
                v-if="menu"
                no-title
                :min="minDate"
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
          <label>To</label>
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="end_time"
                append-icon="event"
                @click="togleDatepicker2"
              ></v-text-field>
              <v-date-picker
                v-model="end_time"
                v-if="menu2"
                no-title
                :min="start_time"
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
          <v-btn
            class="btn--33"
            depressed
            small
            color="#375197"
            style="color:#fff;"
            @click="filterDate"
          >
          Apply Filter
          </v-btn>
          <v-btn
            class="btn--33"
            depressed
            small
            color="#375197"
            style="color:#fff;"
            @click="resetFilter"
          >
          Reset Filter
          </v-btn>
        </div>
      </div>
      <div class="session__header--right resp" v-if="stateResizeScreen == 1">
        <span @click="toggleFilter">Filter By Date <v-icon size="10" color="#49C5FE">fa-chevron-down</v-icon></span>
        <div class="header__filter resp" v-if="handleFilter">
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="start_time"
                append-icon="event"
                @click="togleDatepicker"
              ></v-text-field>
              <v-date-picker
                v-model="start_time"
                v-if="menu"
                no-title
                :min="minDate"
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
          <div class="datepicker--border">
            <v-menu
              ref="menu"
              v-model="menu2"
              :close-on-content-click="false"
              :nudge-right="40"
              lazy
              transition="scale-transition"
              offset-y
              full-width
              min-width="290px"
            >
              <v-text-field
                slot="activator"
                v-model="end_time"
                append-icon="event"
                @click="togleDatepicker2"
              ></v-text-field>
              <v-date-picker
                v-model="end_time"
                v-if="menu2"
                no-title
                :min="start_time"
              >
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="menu2 = false">Cancel</v-btn>
                <v-btn flat color="primary" @click="menu2 = false">OK</v-btn>
              </v-date-picker>
            </v-menu>
          </div>
          <div class="trans__btn--quarterround filter--btn" @click="filterDate">Apply</div>
          <div class="trans__btn--quarterround filter--btn" @click="resetFilter">Reset</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { format, subDays, addDays } from 'date-fns';

export default {
  data () {
    return {
      sessionTabs: 'upcoming',
      range: [],
      menu: false,
      menu2: false,
      start_time: null,
      end_time: null,
      handleFilter: false
    }
  },
  computed: {
    stateResizeScreen () {
      const screen = this.$store.state.stateResizeScreen
      if (screen == null) {
        return 0
      } else {
        return screen
      }
    },
  },
  methods: {
    onDateRangeChange(range) {
      this.range = range;
    },
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
    },
    changeTab (event) {
      this.$emit('clicked', event)
      this.sessionTabs = event
    },
    filterDate () {
      const rangeDate = {
        startdate: this.start_time,
        enddate: this.end_time
      }

      if (this.sessionTabs == "upcoming") {
        this.$store.dispatch("POST_UPCOMING_FILTER_DATE", rangeDate)
      } else {
        this.$store.dispatch("POST_HISTORY_SESSION_DATE", rangeDate)
      }
    },
    resetFilter () {
      if (this.sessionTabs == "upcoming") {
        this.$store.dispatch('GET_UPCOMING_SESSIONS')
      } else {
        this.$store.dispatch('GET_PROFILE_SESSION_HISTORY')
      }
      this.start_time = null
      this.end_time = null
    },
    toggleFilter () {
      this.handleFilter = !this.handleFilter
    },
  }
}
</script>

<style lang="scss">

</style>



