<template>
   <div>
      <div class="BxVideoPlayer" transition="fade-transition" >
         <v-card class="BxVideoPlayer__BxVcard">
            
            <div class="BxVideoPlayer__BxVcard--VideoPlayer">
               <BlueLoader style="height: 400px;width:400px;margin:auto;" v-if="showLottie"/>
               <video-player class="video-player-box"
                     ref="videoPlayer"
                     :options="playerOptions"
                     :playsinline="true"
                     @play="onPlayerPlay($event)"
                     @pause="onPlayerPause($event)"
                     @ended="onPlayerEnded($event)"
                     @loadeddata="onPlayerLoadeddata($event)"
                     @waiting="onPlayerWaiting($event)"
                     @playing="onPlayerPlaying($event)"
                     @timeupdate="onPlayerTimeupdate($event)"
                     @canplay="onPlayerCanplay($event)"
                     @canplaythrough="onPlayerCanplaythrough($event)"
                     @ready="playerReadied"
                     @statechanged="playerStateChanged($event)"
                     v-show="!showLottie"
               >
               </video-player>
            </div>
          
         </v-card>
      </div>
   </div>
</template>

<script>
   import videojs from 'video.js'
   window.videojs = videojs
   import { videoPlayer } from 'vue-video-player'

   require('videojs-contrib-hls/dist/videojs-contrib-hls.js')
   
   import 'vue-video-player/src/custom-theme.css'
   import 'video.js/dist/video-js.css'
   import BlueLoader from '../../views/Lottie/BlueLoading'
   export default {
      components: {
         videoPlayer,
         BlueLoader
      },
      data(){
         return{
            showLottie:false,
            dialog:true,
            dataSrc: "https://agora-dyned.oss-cn-shanghai.aliyuncs.com/1a818cadd84fb4e39799ef97708f1a2d_MTk5MDZkeW5lZC5zdHVkZW50KzFAZ21haWwuY29tMTAwMDEwMzA%3D.m3u8",
            playerOptions: {
            // videojs options
               overNative: true,
               autoplay: false,
               controls: true,
               sourceOrder: true,
               language: 'en',
               playbackRates: [0.7, 1.0, 1.5, 2.0],
               sources: [{
                  withCredentials: false,
                  type: "application/x-mpegURL",
                  src: null
               }],
               // poster: "https://surmon-china.github.io/vue-quill-editor/static/images/surmon-1.jpg",
               controlBar: {
                  timeDivider: false,
                  durationDisplay: false
               },
               flash: { hls: { withCredentials: false }},
               html5: { hls: { withCredentials: false }},
               
            }

         }
      },

      mounted() {
         this.showLottie = true
      },

      computed: {
         player() {
            return this.$refs.videoPlayer.player
         },

         CheckLoad(){
            if (this.$store.getters.CCK_REC_URL_AGORA !== 'IS NULL'){
               const ItemsCont = []
               for (var i in this.$store.getters.CCK_REC_URL_AGORA){
                  ItemsCont.push(this.$store.getters.CCK_REC_URL_AGORA[i])
               }
               return ItemsCont
            }else{
               return 'IS NULL'
            }
         }
      },
      watch: {

         CheckLoad(val){
            const dataCckRecUrl = val[0]
            if (dataCckRecUrl == 200 || dataCckRecUrl == 404){
                this.playerOptions.sources[0].src = this.$store.state.stateRecUrl.result.url
               setTimeout(() => {
                  this.showLottie = false
                 
               }, 1500);
            }
         }
      },
      methods: {
         // listen event
         onPlayerPlay(player) {
         // console.log('player play!', player)
         },
         onPlayerPause(player) {
         //console.log('player pause!', player)
         },
         onPlayerEnded(player) {
         //console.log('player ended!', player)
         },
         onPlayerLoadeddata(player) {
            //console.log(this.playerOptions.sources[0])
         },
         onPlayerWaiting(player) {
         // console.log('player Waiting!', player)
         },
         onPlayerPlaying(player) {
         // console.log('player Playing!', player)
         },
         onPlayerTimeupdate(player) {
         // console.log('player Timeupdate!', player.currentTime())
         },
         onPlayerCanplay(player) {
         // console.log('player Canplay!', player)
         },
         onPlayerCanplaythrough(player) {
         // console.log('player Canplaythrough!', player)
         },
         // or listen state event
         playerStateChanged(playerCurrentState) {
            //console.log('player current update state', playerCurrentState)
         },
         
         // player is ready
         playerReadied(player) {
            var hls = player.tech({ IWillNotUseThisInPlugins: true }).hls
               player.tech_.hls.xhr.beforeRequest = function(options) {
               return options
            }

            this.player.on('error', () => {
               if (this.player.error().code === 4) {
                  errorDisplay: false
                  this.player.createModal('VIDEO IS EXPAIRED');
               }
            });
         }
      },
      destroyed() {
         this.$store.commit('toggleDialogThree', false);
			this.$store.commit('commitRecUrl', null);
         this.$store.commit('muttCckRecUrl', null);
      },
   }
</script>

<style lang="scss">
    @import './AgoraVideoPlayer.scss';
</style>


