<template lang="html">
  <div class="session__resp--list">
    <div class="NocoachAvailable--global" v-if="!sessionListCheck">
       <div class="NoAvailable">
          <p>There's no upcoming session</p>
       </div>
    </div>
    <div class="session__resp--list-each" v-for="(data, i) in sortedItems" :key="i" v-if="sessionListCheck">
      <div class="session__resp--list-collapsed">
        <div class="session__resp--list-left">
          <div class="session__resp--list-time">{{ data.start_time | formatTime }} - {{ data.end_time | formatTime }}</div>
          <div class="session__resp--list-date">{{ data.date | formatDateDay }}</div>
          <div class="session__resp--list-desc overflow--ellipsis">Session with {{ data.coach_name }}</div>
        </div>
      </div>
      <UpcomingListToggle :data="data" />
    </div>
  </div>
</template>

<script>
import { MomentFilter } from '../../../components/mixins/MomentFilter.js'
import UpcomingListToggle from './UpcomingListToggleResp.vue'

export default {
  components: {
    UpcomingListToggle
  },
  mixins: [MomentFilter],
  data: () => ({
    activeIndex: null
  }),
  created() {
    this.$store.dispatch('GET_UPCOMING_SESSIONS')
  },
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.date < b.date)
          return 1;
        if (a.date > b.date)
          return -1;
        return 0;
      }

      // return this.$store.state.stateUpcomingSession.new_appointment.sort(compare);
      const upcomingSession = this.$store.state.stateUpcomingSession
      if (upcomingSession == null) {
        return 0
      } else {
        return upcomingSession.new_appointment.sort(compare);
      }
    },
    sessionListCheck () {
      if (this.sortedItems != 0) {
        return true
      } else {
        return false
      }
    },
  },
  methods: {
    getCoach (i, username, appointment_id) {
      this.activeIndex = i

      const coachDetail = {
        username : username,
        app_id : appointment_id
      }
      this.$store.dispatch('POST_SESSION_DETAIL', coachDetail)
    },
  },
}
</script>

<style lang="css">
</style>



