<template>
    <div id="app">
      <div class="BxLotties">
         <lottie :options="defaultOptions" v-on:animCreated="handleAnimation" class="BxLotties__LottieArea"/>
      </div>
       
        <div style="visibility:hidden">
          <input type="range" value="0.5" min="0" max="3" step="0.5"
                   v-on:change="onSpeedChange" v-model="animationSpeed">
        </div>
    </div>
</template>


<script>
import Lottie from './Lottie';
import * as animationData from '../../staticjson/loading 2 light blue.json';

  export default {
    name: 'app',
    components: {
      'lottie': Lottie
    },
    data() {
      return {
        defaultOptions: {animationData: animationData},
        animationSpeed:  1,
      }
    },

    methods: {
      handleAnimation: function (anim) {
        this.anim = anim;
      },

      stop: function () {
        this.anim.stop();
      },

      play: function () {
        this.anim.play();
      },

      pause: function () {
        this.anim.pause();
      },

      onSpeedChange: function () {
        this.anim.setSpeed(this.animationSpeed);
        
      },


    },
    mounted() {
      this.onSpeedChange()
    },
  }
</script>



