<template lang="html">
  <div class="reschedule__section">
      <div class="NocoachAvailable--global" v-if="!sessionDetailCheck">
         <div class="NoAvailable">
            <p>Choose a session transaction to see it's content</p>
         </div>
      </div>
      <div class="reschedule" v-if="sessionDetailCheck">
        <div class="reschedule__avatar">
          <v-avatar size=68>
            <img
            :src="sessionDetail.user_data[0].avatar"
            :alt="sessionDetail.user_data[0].fullname"
            >
          </v-avatar>
        </div>
        <div class="reschedule__name">
          {{ sessionDetail.user_data[0].fullname }}
        </div>
        <div class="reschedule__rating">
          <v-rating
            :value="Number(sessionDetail.user_data[0].coach_rating)"
            color="yellow darken-3"
            small
            readonly
            half-increments>
          </v-rating>
        </div>
        <!-- <div class="reschedule__tokens">
          4 Tokens Coach
        </div> -->
        <div class="hr--90"></div>
        <div class="reschedule__scheduled">
          <h5>Scheduled in</h5>
          <div class="reschedule__scheduled__date">
            <span>{{ filteredDate }} (UTCH +7)</span>
            <h2>{{ filteredStart }} - {{ filteredEnd }}</h2>
          </div>
        </div>
        <div class="hr--90"></div>
        <div class="reschedule__btn">
          <div class="trans__btn--halfround-purple" @click="changeDialogState" v-if="handleButtonReschedule">Reschedule Session</div>
          <div class="trans__btn--halfround-purple" style="cursor:not-allowed" v-if="!handleButtonReschedule">Rescheduled</div>
        </div>
      </div>
    </div>
</template>

<script>
import moment from 'moment'

export default {
  data () {
    return {
      inputToken: 1,
    }
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialog
    },
    handleLoader () {
      return this.$store.state.stateLoader
    },
    sessionDetailCheck () {
      const detailCheck = this.$store.state.sessionDetail

      if (detailCheck == null) {
        return false
      } else {
        return true
      }
    },
    sessionDetail () {
      const data = this.$store.state.sessionDetail

      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    handleButtonReschedule () {
      const data = this.sessionDetail.appointment[0].status

      if (data == "active") {
        return true
      } else {
        return false
      }
    },
    filteredDate () {
      return this.sessionDetail.appointment[0].date ? moment(this.sessionDetail.appointment[0].date).format('ddd, MMM DD, YYYY') : ''
    },
    filteredStart () {
      const time = this.sessionDetail.appointment[0].start_time;
      const formatted = moment(time, "HH:mm:ss").format("HH:mm");

      return formatted
    },
    filteredEnd () {
      const time = this.sessionDetail.appointment[0].end_time;
      const formatted = moment(time, "HH:mm:ss").format("HH:mm");

      return formatted
    }
  },
  methods: {
    changeDialogState () {
      this.$store.commit('toggleDialog');

      const data = {
        date: this.sessionDetail.appointment[0].date,
        username: this.sessionDetail.appointment[0].coach_username
      }

      // console.log(data);
      this.$store.dispatch('POST_RESCHEDULE_COACH_AVAILABLE', data)
    }
  },
  watch: {
    sessionDetail (i) {
      if (i = null) {
        return 0
      } else {
        return i
      }
    }
    // handleLoader () {
    //   const commitLoader = false
    //
    //   setTimeout(() => (
    //     this.$store.commit("commitSnackbarVal", stateLoader)
    //   ), 3000)
    // }
  }
}
</script>

<style lang="scss" scopped>
  .NoAvailable {
    display: flex;
    flex-direction: column;
    border: 1px dashed $fontDarkGrey !important;
    font-size: 1em;
    padding: 20px;
    width: 80%;
    justify-content: center;
    align-items: center;

    p {
      color: $fontDarkGrey;
      margin: 0;
      text-align: center;
    }
  }
</style>



