<template lang="html">
  <v-dialog width="530" height="550" v-model="showDialog" persistent content-class="BxvideoPlayer">
		<div class='Bxclose'>
			<v-icon @click="ButtonClose">close</v-icon>
		</div>
		<v-card style="border-radius: 3px;">
        <VideoAgoraPlayer />
     	</v-card>
  </v-dialog>
</template>

 

<script>
import VideoAgoraPlayer from '../../../views/AgoraVideoPlayer/AgoraVideoPlayer'

export default {
	components: {
		VideoAgoraPlayer
	},
	
  	computed: {
		showDialog () {
			return this.$store.state.showDialogThree
		}
		
  	},
  	methods: {
		ButtonClose(){
			this.$store.commit('toggleDialogThree', false);
			this.$store.commit('commitRecUrl', null);
			this.$store.commit('muttCckRecUrl', null);
		}
	},
	beforeDestroy() {
		this.ButtonClose()
	},
	destroyed(){
		this.ButtonClose()
	}
}
</script>

 

<style lang="scss">
	.BxvideoPlayer{
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		box-shadow: none !important;
		overflow: hidden;

		.Bxclose{

			width: 100%;
			display: flex;
			justify-content: flex-end;
			height: 30px;

			i{
			
				font-size: 20px;
				font-weight: 600;
				cursor: pointer;
				color: #fff;

				&:hover{
					color:red;
				}
			}
		}
	}
	.video-player-box{
		.video-js{
			width: 100% !important;
			.vjs-big-play-button{
				top:45% !important;
				left:43% !important;
			}
		}
		
	}
</style>


