<template lang="html">
  <div class="">
    <content-loader
      v-if="!handleLoader"
  		:height="736"
  		:width="1024"
  		:speed="2"
      primaryColor="#f3f3f3"
      secondaryColor="#dcdcdc"
  	>
  		<rect x="36.78" y="81.83" rx="0" ry="0" width="431.35" height="45.36" />
  		<rect x="549.9" y="81.83" rx="0" ry="0" width="428.67" height="45.36" />
  		<rect x="37.9" y="155.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="204.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="254.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="303.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="353.83" rx="0" ry="0" width="940" height="43.01" />
  		<rect x="36.9" y="404.83" rx="0" ry="0" width="940" height="43.01" />
  	</content-loader>
    <div class="session" v-if="handleLoader">
      <div class="boxNotif" v-if="notifRes">
         <v-snackbar
          v-model="snackbar"
          :color="notifColor"
          :timeout="timeout"
          top
        >
          <p>{{ notifResMessage.status }}</p>
        </v-snackbar>
      </div>
      <UpcomingSessionFilter @clicked="onClickChild" />
      <div class="session__left__right-container flex--space-between">
        <div class="session--left">
          <!-- SCREEN SIZE >= 1024 -->
          <keep-alive v-if="stateResizeScreen == 3 || stateResizeScreen == 2">
            <SessionHistory v-if="sessionTabs === 'history'" />
            <UpcomingList v-if="sessionTabs === 'upcoming'"/>
          </keep-alive>
          <!-- SCREEN SIZE <= 599 -->
          <keep-alive v-if="stateResizeScreen == 1">
            <SessionHistoryResp v-if="sessionTabs === 'history'" />
            <UpcomingListResp v-if="sessionTabs === 'upcoming'" />
          </keep-alive>
        </div>

        <!-- SCREEN SIZE >= 1024 -->
        <div class="session--right" v-if="stateResizeScreen == 3 || stateResizeScreen == 2">
          <UpcomingReschedule />
        </div>
      </div>
      <UpcomingRescheduleModal @clicked="snackbarVal" />
      <SessionHistoryModal v-if="CheckToogle2" />
      <SessionHistoryModalAgora  v-if="CheckToogle3"/>
    </div>
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader';
import UpcomingSessionFilter from '../../student-components/sessions/upcoming/UpcomingSessionFilter'
import UpcomingReschedule from '../../student-components/sessions/upcoming/UpcomingReschedule'
import UpcomingList from '../../student-components/sessions/upcoming/UpcomingList'
import SessionHistory from '../../student-components/sessions/history/SessionHistory.vue'
import SessionHistoryModal from '../../student-components/sessions/history/SessionHistoryModal.vue'
import SessionHistoryModalAgora from '../../student-components/sessions/history/SessionHistoryModalAgora.vue'
import UpcomingRescheduleModal from '../../student-components/sessions/upcoming/UpcomingRescheduleModal'
import UpcomingNotif from '../../student-components/sessions/upcoming/UpcomingNotif'
import SessionHistoryResp from '../../student-components/sessions/history/SessionHistoryResp.vue'
import UpcomingListResp from '../../student-components/sessions/upcoming/UpcomingListResp'

export default {
  components: {
    ContentLoader,
    SessionHistory,
    SessionHistoryModal,
    SessionHistoryModalAgora,
    UpcomingSessionFilter,
    UpcomingReschedule,
    UpcomingList,
    UpcomingRescheduleModal,
    UpcomingNotif,
    SessionHistoryResp,
    UpcomingListResp
  },
  data () {
    return {
      showDate: false,
      startDate: null,
      endDate: null,
      modal: false,
      snackbar: false,
      timeout: 3000,
      sessionTabs: 'upcoming',
      ResizeScreen: null
    }
  },
  created() {
    this.$store.dispatch('GET_UPCOMING_SESSIONS')
    this.$store.dispatch('GET_PROFILE_SESSION_HISTORY')
  },
  mounted () {
    this.$nextTick( () => {
       window.addEventListener('resize', this.getWindowWidth);
       window.addEventListener('resize', this.getWindowHeight);

       this.getWindowWidth()
       this.getWindowHeight()
    });
  },
  computed: {
    CheckToogle2(){
      return this.$store.state.showDialogTwo
    },
    CheckToogle3(){
      return this.$store.state.showDialogThree
    },
    handleLoader () {
      const data = this.$store.state.stateUpcomingSession

      if (data == null) {
        return false
      } else {
        return true
      }
    },
    stateResizeScreen () {
      const screen = this.$store.state.stateResizeScreen
      if (screen == null) {
        return 0
      } else {
        return screen
      }
    },
    notifRes() {
      return this.$store.state.rescheduleResponse !== null
    },
    notifResMessage() {
      return this.$store.state.rescheduleResponse
      // console.log(resCoins);
    },
    notifColor() {
      const resStatus = this.$store.state.rescheduleResponse

      if (resStatus.status == "success") {
        return "success"
      } else {
        return "error"
      }
    }
  },
  methods: {
    onClickChild (value) {
      this.sessionTabs = value // someValue
    },
    snackbarVal (value) {
      return this.snackbar = value
      console.log(value);
    },
    getWindowWidth(event){
       this.windowWidth = document.documentElement.clientWidth;

       if (this.windowWidth <= 599){
       //this for resolution phone only
       const resizeScreen = 1
       this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth <= 768){
          //this for resolution Ipad Potrait
          const resizeScreen = 2
          this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth >= 1024){
          //this for resolution Dekstop-up
          const resizeScreen = 3
          this.$store.commit('commitResizeScreen', resizeScreen)
       }
    },
    getWindowHeight(event){
       this.windowHeigt = document.documentElement.clientHeight;
    }
  },
  beforeDestroy() {
     window.removeEventListener('resize', this.getWindowWidth);
     window.removeEventListener('resize', this.getWindowHeight);
  }
}
</script>

<style lang="scss">
  @import './Session.scss'
</style>



