<template lang="html">
  <v-dialog width="500" height="500" v-model="showDialog" persistent>
     <v-card style="background-color:#fff;border-radius: 3px;">
        <v-btn icon light @click="changeDialogState">
          <v-icon>close</v-icon>
        </v-btn>
        <div class="history__modal">
           <div class="" v-if="getRecUrl == null">
             Loading..
           </div>
           <div class="" v-if="getRecUrl != null">
             <video width="320" height="240" controls v-if="handleRecURl != 0 && handleRecURl != 1">
                <source :src="handleRecURl" type="video/mp4">
                Your browser does not support the video tag.
             </video>
             <div class="vid__exp" v-if="handleRecURl == 0">
               <span>Video is still uploading / has been expired</span>
             </div>
             <div class="vid__exp" v-if="handleRecURl == 1">
               <span>Video has been expired</span>
             </div>
           </div>
        </div>
     </v-card>
  </v-dialog>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  computed: {
    showDialog () {
      return this.$store.state.showDialogTwo
    },
    getRecUrl () {
      return this.$store.state.stateRecUrl
    },
    handleRecURl () {
      if (this.getRecUrl.result.length == 0) {
        return 0
      } else if (this.getRecUrl.result[0].url == null) {
        return 1
      } else {
        return this.getRecUrl.result[0].url
      }
    }
  },
  methods: {
    changeDialogState () {
      this.$store.commit('toggleDialogTwo');
      this.$store.commit('commitRecUrl', null);
    },
  }
}
</script>

<style lang="scss">
</style>



