<script>
import moment from 'moment'
import { Glide, GlideSlide } from 'vue-glide-js'
import InfoIcon from '../../../components/icons/InfoIcon'
import Lottie from '../../../components/loading/Lottie.vue'
import * as animationData from '../../../staticjson/animation-w300-h169.json';

export default {
  components: {
    InfoIcon,
    Lottie,
    [Glide.name]: Glide,
    [GlideSlide.name]: GlideSlide
  },
  data () {
    return {
      selectitemBy :['Filter by..', 'Country', 'Date', 'Name', 'Spoken Language'],
      modal: false,
      checked: false,
      menu: false,
      menu2: false,
      currentCoach: null,
      selectedCoach: null,
      toggleCoach: false,
      newCoach: null,
      checkCoachResult: false,
      minDate: new Date().toISOString().substr(0, 10),
      date: null,
      filterDate: null,
      time: new Date(),
      currentTime: null,
      currentTimeIndex: null,
      timepicker: null,
      selectedTime: '',
      toggleSummary: false,
      showLottie: false,
      defaultOptions: {animationData: animationData},
      arrayEvents: null,
      selectBy: "",
      showCountry: false,
      showDate: false,
      showName: false,
      showSpoken: false,
      handleFilter: false,
      show: false
    }
  },
  created () {
    // const currentDate = this.$store.state.sessionDetail.appointment[0].date
    // console.log(currentDate);
    // this.$store.dispatch('POST_RESCHEDULE_COACH_AVAILABLE', currentDate)
  },
  mounted () {
    setTimeout(() => {
      this.myData = 'Example Data';
    }, 3000)
    // console.log(this.$store.state.DataAdditional)
    this.$nextTick( () => {
       window.addEventListener('resize', this.getWindowWidth);
       window.addEventListener('resize', this.getWindowHeight);

       this.getWindowWidth()
       this.getWindowHeight()
    });
  },
  computed: {
    stateResizeScreen () {
      const screen = this.$store.state.stateResizeScreen
      if (screen == null) {
        return 0
      } else {
        return screen
      }
    },
    showDialog () {
      return this.$store.state.showDialog
    },
    // TO GET DATA FOR CURRENT COACH
    sessionDetail () {
      return this.$store.state.sessionDetail
    },
    // TO GET DATA OF LIST COACH
    GET_CoachResult(){
      // return this.$store.state.rescheduleCoachAvailable
      const coachAvailable = this.$store.state.rescheduleCoachAvailable
      if (coachAvailable == null) {
        return 0
      } else {
        return coachAvailable
      }
    },
    getStartEndTime () {
      return this.$store.state.stateStartEndTime
    },
    // TO FORMAT DATE FROM DATEPICKER TO DAY FORMAT
    computedDateFormattedMomentjs () {
       return this.filterDate ? moment(this.filterDate).format('dddd') : ''
    },
    // TO GET IF USER ALREADY SELECTED NEW COACH OR STAY WITH PREVIOUS COACH
    conditionCoachReschedule () {
      const currentCoach = this.$store.state.sessionDetail.user_data[0].username
      const changedCoach = this.selectedCoach

      if (this.toggleCoach == true) {
        return changedCoach
      } else {
        return currentCoach
      }
      return currentCoach
    },
    // TO CHECK AVAILABLE TIME AFTER CLICK ON DATEPICKER
    coachCheckRescheduleTime () {
       const data = this.$store.state.rescheduleTimeAvailable

       if (data == null) {
         return '-'
       } else if (data.status == 'success') {
         return data.coach_schedule
       }
    },
    // TO TRIGGER CONDITION RENDERING FOR SCHEDULE AVAILABLE
    coachCheckRescheduleData () {
      const data = this.$store.state.rescheduleTimeAvailable
      if (data == null) {
        return false
      } else if (data.message == "Schedule not found") {
        return false
      } else if (data.status == "success") {
        return true
      }
      // return this.coachCheckRescheduleTime
    },
    // TO SHOW BOOKING SUMMARY
    coachCheckRescheduleSummary () {
       if (this.coachCheckRescheduleTime == null) {
         return false
       } else {
         return true
       }
    },
    filteredDate () {
      return this.sessionDetail.appointment[0].date ? moment(this.sessionDetail.appointment[0].date).format('MMM DD YYYY') : ''
    },
    filteredDateSummary () {
      return this.date ? moment(this.date).format('MMM DD YYYY') : ''
    },
    filteredDateDay () {
      return this.date ? moment(this.date).format('dddd') : ''
    },
    filteredStart () {
      const time = this.sessionDetail.appointment[0].start_time;
      const formatted = moment(time, "HH:mm:ss").format("HH:mm");

      return formatted
    },
    filteredEnd () {
      const time = this.sessionDetail.appointment[0].end_time;
      const formatted = moment(time, "HH:mm:ss").format("HH:mm");

      return formatted
    },
    //FOR LOTTIE
    handleAnimation: function (anim) {
       this.anim = anim;
    },
    coachToken () {
      const data = this.sessionDetail.user_data[0].coach_type

      if (data == "standard") {
        return 2
      } else if (data == "elite") {
        return 4
      }
    }
  },
  methods: {
    getWindowWidth(event){
       this.windowWidth = document.documentElement.clientWidth;

       if (this.windowWidth <= 599){
       //this for resolution phone only
       const resizeScreen = 1
       this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth <= 768){
          //this for resolution Ipad Potrait
          const resizeScreen = 2
          this.$store.commit('commitResizeScreen', resizeScreen)
       }else if(this.windowWidth >= 1024){
          //this for resolution Dekstop-up
          const resizeScreen = 3
          this.$store.commit('commitResizeScreen', resizeScreen)
       }
    },
    getWindowHeight(event){
       this.windowHeigt = document.documentElement.clientHeight;
    },
    // ACTION FOR CANCEL BUTTON
    changeDialogState () {
      this.$store.commit('toggleDialogFalse')
      this.$store.commit('coachAvailableNull')
      this.date = null
      this.checked = false
      this.menu = false
    },
    toggleFilter () {
      this.handleFilter = !this.handleFilter
    },
    // ACTION TO GET LIST OF COACH
    getCoachReschedule () {
      this.checkCoachResult = false
      this.toggleCoach = !this.toggleCoach
    },
    rescheduleDate(filterDate) {
      const data = filterDate
      // console.log(data);
      this.$store.dispatch('POST_RESCHEDULE_DATE', data)
    },
    // ACTION TO GET LIST COACH INDEX AND DATA
    clickedCoach(data, index) {
      this.currentCoach = index
      this.selectedCoach = data.username
    },
    // ACTION TO CHECK IF USER WANT TO CHANGE COACH OR NO
    checkNewCoach () {
      this.checkCoachResult = true
    },
    // ACTION TO TOGGLE DATEPICKER
    togleDatepicker () {
      this.menu = !this.menu
    },
    togleDatepicker2 () {
      this.menu2 = !this.menu2
      this.modal = !this.modal
    },
    // ACTION TO CHECK COACH SHCEDULE
    checkRescheduleCoach () {
       const CheckReschedule = {
          day: this.computedDateFormattedMomentjs,
          username: this.conditionCoachReschedule,
          date: this.date
       }
       console.log(CheckReschedule);
       this.$store.dispatch('POST_CHECK_RESCHEDULE_COACH', CheckReschedule);
    },
    // ACTION TO TOGGLE BOOKING SUMMARY
    showSummary () {
      this.toggleSummary = true
    },
    // ACTION POST RESCHEDULE SESSION
    postReschedule () {
      const rescheduleData = {
        time: this.currentTime,
        username: this.conditionCoachReschedule,
        day: this.filteredDateDay,
        date: this.date,
        app_id: this.sessionDetail.appointment[0].appointment_id
      }
      // console.log(rescheduleData);

      this.$store.dispatch('POST_CONFIRM_RESCHEDULE', rescheduleData)
      this.$store.commit('toggleDialogFalse')
      this.$emit('clicked', 'true')
      this.$store.commit('coachAvailableNull')
      this.date = null
      this.checked = false
      this.menu = false

      setTimeout(()=>{
        this.$store.dispatch('GET_UPCOMING_SESSIONS')
        this.$store.commit('storeSessionDetail', null)
        this.$store.commit('storeTimeRescheduleAvailable', null)
        this.toggleCoach = false
        this.toggleSummary = false
        this.currentCoach = null
      }, 2000)
    },
    // ACTION TO TRIGGER LOTTIE LOADER AND TO CHECK COACH SCHEDULE
    showLottieLoader () {
       this.checkRescheduleCoach()
       this.showLottie = true
       setTimeout(()=>{
         this.showLottie = false
       }, 1000)
    },
    showFilter (i, data) {
      this.selectBy = data
    },
    currentSelectTime (data, i) {
      // console.log("data log " + data);
      // console.log("i log " + i);
      this.currentTimeIndex = i
      this.currentTime = data
      this.currentTime = this.selectedTime
    }
  },
  watch: {
    // ACTION TO WATCH EVERYTIME USER CLICKED ON NEW COACH
    newCoach (val) {
      this.checkNewCoach()
    },
    // ACTION TO WATCH EVERYTIME USER CLICKED ON DATE
    date (val) {
      this.current = null
      this.toggleSummary = false
      this.filterDate = val
      // this.checkRescheduleCoach()
      this.showLottieLoader()
    },
    // filterDate (val) {
    //   if (!val) return
    //   this.$store.commit('storeFilterGetData',{
    //     TheDate : this.date,
    //     day :  this.GetFormatDay
    //   })
    // },
    // ACTION TO WATCH EVERYTIME USER CLICKED ON COACH SCHEDULE AND TO SHOW BOOKING SUMMARY
    timepicker (val) {
      this.showSummary()
    }
  }
}
</script>

