    width: 95%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
  .datepicker--border {

    & .v-input__slot {
      margin-bottom: 3px;
      border: 1px solid $lightgrey;
      border-radius: 2px;
      background-color: #fff;

      &::before {
        border-style: none !important;
      }

      & .v-text-field__slot {

        input {
          padding: 8px 10px;
        }
      }
    }
  }
  .datepicker--border-modal {
    width: 100% !important;

    .v-menu {
      width: 100%;
    }
    .v-text-field.v-text-field--solo .v-input__control {
      height: 34px !important;
      min-height: 34px !important;
    }
    .theme--light.v-text-field--solo .v-input__slot {
      border-radius: 5px;
      box-shadow: none;
      background: transparent;
    }
    .theme--light.v-label {
      color: $black !important;
      font-size: .9em;
    }
    .theme--light.v-input:not(.v-input--is-disabled) input, .theme--light.v-input:not(.v-input--is-disabled) textarea {
      color: $black !important;
      font-size: .9em;
    }
    .theme--light.v-icon {
      color: $black !important;
    }
    .v-text-field.v-text-field--solo:not(.v-text-field--solo-flat) .v-input__slot {
      box-shadow: none !important;
    }
  }
  .coach__profile__tokens {

    & font {

      @media screen and (max-width: 599px) {
        font-size: 1.5em;
      }
    }
  }
  .coach__active {
    color: $black !important;
    background-color: $white;
    border: 1px solid $black;

    & .coach__profile--small {

      & .coach__profile__name, & .coach__profile__tokens {
        color: $black;

        & font {

          @media screen and (max-width: 599px) {
            font-size: 1.5em;
          }
        }
      }
    }
  }
</style>



