<template lang="html">
  <div class="table__list">
    <div class="NocoachAvailable--global" v-if="!sessionListCheck">
       <div class="NoAvailable">
          <p>There's no upcoming session</p>
       </div>
    </div>
    <table style="width:100%" v-if="sessionListCheck">
      <tr>
        <th>Transaction</th>
        <th>Coach</th>
        <th>Session Date</th>
        <th>Action</th>
      </tr>
      <tr
        v-for="(data, i) in sortedItems"
        :key="i"
        @click="getCoach(i, data.username, data.appointment_id)"
        :class="{'table--active': i == activeIndex}"
      >
        <td align="center">{{ data.created | formatDate }}</td>
        <td align="center">{{ data.coach_name }}</td>
        <td align="center">{{ data.date | formatDate }}, {{ data.start_time | formatTime }} - {{ data.end_time | formatTime }}</td>
        <td align="center"><div class="trans__btn--halfround-purple">View Details</div></td>
      </tr>
    </table>
  </div>
</template>

<script>
import { MomentFilter } from '../../../components/mixins/MomentFilter.js'

export default {
  mixins: [MomentFilter],
  data () {
    return {
      activeIndex: null
    }
  },
  methods: {
    getCoach (i, username, appointment_id) {
      this.activeIndex = i

      const coachDetail = {
        username : username,
        app_id : appointment_id
      }
      this.$store.dispatch('POST_SESSION_DETAIL', coachDetail)
      this.$store.commit('commitLoader', true)
    },
  },
  computed: {
    sortedItems() {
      function compare(a, b) {
        if (a.date < b.date)
          return 1;
        if (a.date > b.date)
          return -1;
        return 0;
      }

      // return this.$store.state.stateUpcomingSession.new_appointment.sort(compare);
      const upcomingSession = this.$store.state.stateUpcomingSession
      if (upcomingSession == null) {
        return 0
      } else {
        return upcomingSession.new_appointment.sort(compare);
      }
    },
    sessionListCheck () {
      if (this.sortedItems != 0) {
        return true
      } else {
        this.$store.state.sessionDetail = null
        return false
      }
    },
  },
}
</script>

<style lang="scss">
</style>



