<template lang="html">
  <div>
    <v-icon size="10" color="#fff" v-if="!toggleDetail"  @click="toggleDetails(i, data.username, data.appointment_id)">fa-chevron-down</v-icon>
    <div class="session__resp--list-ellapsed reschedule" v-if="toggleDetail">
      <div class="reschedule__resp--left">
        <div class="reschedule__avatar">
          <v-avatar size=68>
            <img
            :src="sessionDetail.user_data[0].avatar"
            :alt="sessionDetail.user_data[0].fullname"
            >
          </v-avatar>
        </div>
        <div class="reschedule__modal__right__detail">
          <div class="reschedule__name overflow--ellipsis">
            {{ sessionDetail.user_data[0].fullname }}
          </div>
          <div class="reschedule__rating">
            <v-rating
              :value="Number(sessionDetail.user_data[0].coach_rating)"
              color="yellow darken-3"
              small
              readonly
              half-increments>
            </v-rating>
          </div>
          <div class="reschedule__tokens">
            4 Tokens Coach
          </div>
        </div>
      </div>
      <div class="trans__btn--halfround" @click="changeDialogState" v-if="handleButtonReschedule">Reschedule</div>
      <div class="trans__btn--halfround" v-if="!handleButtonReschedule">Rescheduled</div>
    </div>
    <v-icon size="10" color="#fff" v-if="toggleDetail" @click="toggleDetails">fa-chevron-up</v-icon>
  </div>
</template>

<script>
export default {
  props: ['data'],
  data: () => ({
    toggleDetail: false
  }),
  computed: {
    sessionDetail () {
      const data = this.$store.state.sessionDetail

      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    handleButtonReschedule () {
      const data = this.sessionDetail.appointment[0].status

      if (data == "active") {
        return true
      } else {
        return false
      }
    },
  },
  methods: {
    toggleDetails (i, username, appointment_id) {
      this.toggleDetail = !this.toggleDetail

      const coachDetail = {
        username : username,
        app_id : appointment_id
      }
      this.$store.dispatch('POST_SESSION_DETAIL', coachDetail)
    },
    changeDialogState () {
      this.$store.commit('toggleDialog');
      const data = {
        date: this.sessionDetail.appointment[0].date,
        username: this.sessionDetail.appointment[0].coach_username
      }
      this.$store.dispatch('POST_RESCHEDULE_COACH_AVAILABLE', data)
    }
  }
}
</script>

<style lang="css">
</style>



